import React, { useState, useEffect } from "react";
import { communityData } from "../data/communityData"; // Import d liu cng ng
import "../styles/Community.css"; // Import CSS cho cng ng

const Community = () => {
  const [communities, setCommunities] = useState([]);

  useEffect(() => {
    // Set d liu cng ng khi trang c load
    setCommunities(communityData); // Bn c th thay th bng API nu cn
  }, []);

  return (
    <div className="community">
      <h2>Cng ng Cosplay ti Vit Nam</h2>
      <div className="community-grid">
        {communities.map((community) => (
          <div key={community.id} className="community-card">
            <h3>{community.name}</h3>
            <p>{community.description}</p>
            {/* Hin th hnh nh v lin kt */}
            <a href={community.imageLink} target="_blank" rel="noopener noreferrer">
              <img
                className="community-image"
                src={community.image}
                alt={community.name}
              />
            </a>
            <p>
              Tham gia cng ng ti:{" "}
              <a href={community.imageLink} target="_blank" rel="noopener noreferrer">
                Facebook
              </a>
            </p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Community;
