// src/pages/Events.js
import React, { useState, useEffect } from "react";
import EventCard from "../components/EventCard"; // Import component EventCard  hin th s kin
import { eventsData } from "../data/eventsData"; // Import d liu s kin
import "../styles/Events.css"; // Import CSS cho s kin

const Events = () => {
  const [events, setEvents] = useState([]);

  useEffect(() => {
    // Set s kin mu vo state khi trang c load
    setEvents(eventsData); // Bn c th thay th bng API nu cn
  }, []);

  return (
    <div className="events">
      <h2>S kin Cosplay sp ti ti Vit Nam</h2>
      <div className="event-grid">
        {events.map((event) => (
          <EventCard key={event.id} event={event} />
        ))}
      </div>
    </div>
  );
};

export default Events;
